/* Глобальные блоки */

.hamb-container {
  display: none;
  cursor: pointer;
  position: absolute;
  z-index: 105;
  right: 35px;
  height: 27px;
  width: 35px;
}

.hamb-container:active,
.hamb-container:focus {
  outline: #2B68B4;
}

.hamb-container:before,
.hamb-container:after {
  content: '';
  position: absolute;
  z-index: 105;
  display: block;
  width: 35px;
  height: 5px;
  left: 0;
  background-color: #357dd6;
  border: none;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

.hamb-container:before {
  top: 0;
}

.hamb-container:after {
  bottom: 0;
}

.hamb-container.isopened {
  position: fixed;
  top: 40px !important;
  margin: 0;
}

.hamb-container.isopened .menu-hamburger {
  opacity: 0;
}

.hamb-container.isopened:before {
  transform: rotate(45deg) translateY(13.5px);
  transform-origin: 50% 7.5px;
}

.hamb-container.isopened:after {
  transform-origin: 50% -2.5px;
  transform: rotate(-45deg) translateY(-13.5px);
}

.menu-hamburger {
  display: none;
  width: 35px;
  height: 5px;
  background-color: #357dd6;
  border: none;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  position: absolute;
  top: 50%;
  margin-top: -2.5px;
  left: 0;
  transition: 0.3s;
}

.menu-hamburger:active,
.menu-hamburger:focus {
  outline: none;
}

.headers {
  text-transform: uppercase;
  color: #aaaaaa;
  line-height: 1.1;
  text-align: center;
  font-size: 42px;
  font-weight: bold;
  width: 98%;
