}

@supports (-webkit-text-fill-color: transparent) {
  .headers {
    background: linear-gradient(#fff, #585858 70%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-transform: uppercase;
    color: #aaaaaa;
    line-height: 1.1;
    text-align: center;
    font-size: 42px;
    font-weight: bold;
    width: 98%;
    margin: auto;
  }

@media screen and (max-width: 500px) {
    .headers {
      font-size: 30px !important;
    }
}
}

.line {
  position: relative;
  z-index: 1;
  padding-bottom: 20px;
}

.line:before {
  content: '';
  display: block;
  position: absolute;
  z-index: 2;
  background: linear-gradient(to right, #000, #0f0f0f 40%, #555 50%, #0f0f0f 60%, #000);
  bottom: 0;
  height: 1px;
  left: 0;
  width: 100%;
}

.line_blue {
  position: relative;
  z-index: 1;
  padding-bottom: 10px;
}

.line_blue:after {
  content: '';
  position: absolute;
  z-index: 2;
  display: block;
  bottom: 0;
  left: 0;
  width: 20px;
  border: 1px solid #3c7ac8;
}

